// базовые подключения
@use "sass:math";

@import "vars";
@import "mixins";
@import "fonts";
@import "settings";




body {


   &._lock {
      overflow: hidden;
      touch-action: none;
      overscroll-behavior: none;
   }

   // Сайт замонтирован
   .loaded & {}
}



.wrapper {
   position: relative;
   min-height: 100%;
   display: flex;
   flex-direction: column;
   overflow: hidden;


   &::after {
      content: '';
      position: absolute;
      z-index: -5;
      top: 0;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: rgb(0, 0, 0);
   }


   @supports (overflow: clip) {
      overflow: clip;
   }

   // Прижимаем footer
   >main {
      flex: 1 1 auto;

   }

   // фикс для слайдеров
   >* {
      min-width: 0;
   }
}




[class*="__container"] {
   margin: 0 auto;
   padding: 0 var(--container-offset);
   max-width: var(--container-width);
}



// подключения компонентов страницы

@import "./components/common";

@import "./components/header";
@import "./components/hero";
@import "./components/footer";