.hero {
   padding-top: toRem(90);
   padding-bottom: toRem(140);
   display: flex;

   @media (max-width: toEm(1536)) {
      padding-bottom: toRem(90);
      align-items: center;
   }

   @media (max-width: toEm(1024)) {
      align-items: center;
      padding-bottom: toRem(50);
   }

   //.hero__container

   &__container {
      // position: relative;
      width: 100%;
      display: flex;

      @media (max-width: toEm(1024)) {
         flex-direction: column;
         align-items: start;
      }
   }

   // .hero__main

   &__main {
      &:not(:last-child) {
         margin-bottom: toRem(20);
      }

      margin-top: auto;
      padding-bottom: toRem(120);
      position: relative;
      z-index: 5;
      width: 50%;

      @media (max-width: toEm(1536)) {
         padding-bottom: 0;
      }


      @media (max-width: toEm(1024)) {
         width: 100%;
         padding-bottom: 0;
      }
   }

   // .hero__text

   &__text {
      margin-bottom: toRem(11);
      font-weight: 300;
      font-size: toRem(24);

      @media (max-width: toEm(1536)) {
         font-size: toRem(20);
      }


      opacity: 0;
      transform: translate(0px, toRem(-20));

      ._loaded & {
         transition: transform 1.6s ease, opacity 1.6s ease;
         transform: translate(0px, 0);
         opacity: 1;
      }
   }

   // .hero__title

   &__title {
      font-weight: 600;
      font-size: toRem(86);
      line-height: 100%;

      @media (max-width: toEm(1536)) {
         font-size: toRem(68);
      }

      @media (max-width: toEm(768)) {
         font-size: toRem(40);
      }

      opacity: 0;
      transform: translate(0px, toRem(30));

      ._loaded & {
         transition: transform 1.6s ease 0.5s, opacity 1.6s ease 0.5s;
         transform: translate(0px, 0);
         opacity: 1;
      }
   }


   // .hero__video

   &__video {
      mix-blend-mode: lighten;

      video {
         width: 100%;
         height: 100%;
         object-fit: contain;
      
         display: block;


      }

      @media (min-width: toEm(1025)) {
         position: absolute;
         top: 52%;
         transform: translate(0, -50%);
         right: 0;
         width: 50%;
         overflow: hidden;
      }

      @media (max-width: toEm(1024)) {
         margin-left: toRem(-20);
         margin-right: toRem(-15);
      }
   }
}