.footer {
	position: absolute;
	width: 100%;
	bottom: 0;
   padding-top: toRem(50);
   padding-bottom: toRem(50);

   @media (max-width: toEm(1536)) {
      padding-top: toRem(20);
      padding-bottom: toRem(20);
   }

   // .footer__container

   &__container {}
}

.socials {
   display: flex;
   gap: toRem(14);
   // .socials__item

   &__item {
      transition: transform 0.3s ease 0s;

      @media (any-hover: hover) {
         &:hover {
            transform: scale(1.08);
         }
      }
   }

   // .socials__icon

   &__icon {
      width: toRem(48);
      height: toRem(48);

		img {
			width: 100%;
		}

      @media (max-width: toEm(768)) {
         width: toRem(35);
         height: toRem(35);
      }

   }
}