.header {
   position: absolute;
   width: 100%;
   top: 0;
   z-index: 20;
   padding-top: toRem(35);
   padding-bottom: toRem(35);

   @media (max-width: toEm(1536)) {
      padding-top: toRem(20);
      padding-bottom: toRem(20);
   }

   // .header__container

   &__container {
	
      display: flex;
      align-items: center;
      justify-content: space-between;
      gap: toRem(15);
   }

   // .header__logo

   &__logo {
      display: flex;
      align-items: center;
      gap: toRem(7);
      width: toRem(46);
      aspect-ratio: 1/1;
      font-family: var(--second-family);
      font-weight: 500;
      font-size: toRem(20);

      img {
         width: 100%;
      }
   }

   // .header__actions

   &__actions {}

   // .header__button

   &__button {
      padding: toRem(7) toRem(35);
      display: inline-flex;
      align-items: center;
      justify-content: center;
      border-radius: 500px;
      background: #000;
      border: 1px solid #dadada;
      color: var(--main-color);
      min-height: toRem(40);
      font-weight: 700;
      text-transform: uppercase;
      font-size: toRem(12);
      transition: border 0.3s ease;

      @media (max-width: toEm(768)) {
         padding: toRem(7) toRem(20);
         min-height: toRem(36);
      }

      &:focus-visible {
         outline: 2px solid var(--main-color);
      }

      &:active {
         outline: 2px solid var(--main-color);
      }

      @media (any-hover: hover) {
         &:hover {
            border: 1px solid #FB8F1F;
         }
      }
   }
}