@font-face {
  font-family: "VAG World";
  src: url("../fonts/../fonts/vag_world_bold.woff2") format("woff2");
  font-weight: 700;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "Edo SZ HQ";
  src: url("../fonts/../fonts/edoszhq_regular.woff2") format("woff2");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}
* {
  padding: 0px;
  margin: 0px;
  border: 0px;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

*::before,
*::after {
  display: inline-block;
}

img {
  vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: inherit;
  font-size: inherit;
}

ul li {
  list-style: none;
}

a {
  display: inline-block;
  color: inherit;
  text-decoration: none;
}

input,
button,
textarea {
  font-family: var(--font-family, sans-serif);
  font-size: inherit;
  line-height: inherit;
  color: inherit;
  background-color: transparent;
}

button,
button:focus,
button:active {
  -webkit-tap-highlight-color: transparent;
}

a,
a:active {
  -webkit-tap-highlight-color: transparent;
}

input,
textarea {
  width: 100%;
}

button,
select,
option {
  cursor: pointer;
}

html,
body {
  height: 100%;
  min-width: 20rem;
  scroll-behavior: smooth;
}

body {
  color: var(--main-color);
  font-family: var(--font-family, sans-serif);
  font-size: 1rem;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

input {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
}
input::-webkit-search-decoration, input::-webkit-search-cancel-button, input::-webkit-search-results-button, input::-webkit-search-results-decoration {
  display: none;
}

input[type=number]::-webkit-inner-spin-button,
input[type=number]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input[type=number] {
  -moz-appearance: textfield;
}

input[type=number]:hover,
input[type=number]:focus {
  -moz-appearance: textfield;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

body._lock {
  overflow: hidden;
  touch-action: none;
  overscroll-behavior: none;
}
.wrapper {
  position: relative;
  min-height: 100%;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}
.wrapper::after {
  content: "";
  position: absolute;
  z-index: -5;
  top: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgb(0, 0, 0);
}
@supports (overflow: clip) {
  .wrapper {
    overflow: clip;
  }
}
.wrapper > main {
  flex: 1 1 auto;
}
.wrapper > * {
  min-width: 0;
}

[class*=__container] {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.background {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  z-index: -1;
}
.background::after {
  content: "";
  position: absolute;
  z-index: 1;
  top: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.15);
}
.background img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: 50%;
}

.header {
  position: absolute;
  width: 100%;
  top: 0;
  z-index: 20;
  padding-top: 2.1875rem;
  padding-bottom: 2.1875rem;
}
@media (max-width: 96em) {
  .header {
    padding-top: 1.25rem;
    padding-bottom: 1.25rem;
  }
}
.header__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 0.9375rem;
}
.header__logo {
  display: flex;
  align-items: center;
  gap: 0.4375rem;
  width: 2.875rem;
  aspect-ratio: 1/1;
  font-family: var(--second-family);
  font-weight: 500;
  font-size: 1.25rem;
}
.header__logo img {
  width: 100%;
}
.header__button {
  padding: 0.4375rem 2.1875rem;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  border-radius: 500px;
  background: #000;
  border: 1px solid #dadada;
  color: var(--main-color);
  min-height: 2.5rem;
  font-weight: 700;
  text-transform: uppercase;
  font-size: 0.75rem;
  transition: border 0.3s ease;
}
@media (max-width: 48em) {
  .header__button {
    padding: 0.4375rem 1.25rem;
    min-height: 2.25rem;
  }
}
.header__button:focus-visible {
  outline: 2px solid var(--main-color);
}
.header__button:active {
  outline: 2px solid var(--main-color);
}
@media (any-hover: hover) {
  .header__button:hover {
    border: 1px solid #FB8F1F;
  }
}

.hero {
  padding-top: 5.625rem;
  padding-bottom: 8.75rem;
  display: flex;
}
@media (max-width: 96em) {
  .hero {
    padding-bottom: 5.625rem;
    align-items: center;
  }
}
@media (max-width: 64em) {
  .hero {
    align-items: center;
    padding-bottom: 3.125rem;
  }
}
.hero__container {
  width: 100%;
  display: flex;
}
@media (max-width: 64em) {
  .hero__container {
    flex-direction: column;
    align-items: start;
  }
}
.hero__main {
  margin-top: auto;
  padding-bottom: 7.5rem;
  position: relative;
  z-index: 5;
  width: 50%;
}
.hero__main:not(:last-child) {
  margin-bottom: 1.25rem;
}
@media (max-width: 96em) {
  .hero__main {
    padding-bottom: 0;
  }
}
@media (max-width: 64em) {
  .hero__main {
    width: 100%;
    padding-bottom: 0;
  }
}
.hero__text {
  margin-bottom: 0.6875rem;
  font-weight: 300;
  font-size: 1.5rem;
  opacity: 0;
  transform: translate(0px, -1.25rem);
}
@media (max-width: 96em) {
  .hero__text {
    font-size: 1.25rem;
  }
}
._loaded .hero__text {
  transition: transform 1.6s ease, opacity 1.6s ease;
  transform: translate(0px, 0);
  opacity: 1;
}
.hero__title {
  font-weight: 600;
  font-size: 5.375rem;
  line-height: 100%;
  opacity: 0;
  transform: translate(0px, 1.875rem);
}
@media (max-width: 96em) {
  .hero__title {
    font-size: 4.25rem;
  }
}
@media (max-width: 48em) {
  .hero__title {
    font-size: 2.5rem;
  }
}
._loaded .hero__title {
  transition: transform 1.6s ease 0.5s, opacity 1.6s ease 0.5s;
  transform: translate(0px, 0);
  opacity: 1;
}
.hero__video {
  mix-blend-mode: lighten;
}
.hero__video video {
  width: 100%;
  height: 100%;
  object-fit: contain;
  display: block;
}
@media (min-width: 64.0625em) {
  .hero__video {
    position: absolute;
    top: 52%;
    transform: translate(0, -50%);
    right: 0;
    width: 50%;
    overflow: hidden;
  }
}
@media (max-width: 64em) {
  .hero__video {
    margin-left: -1.25rem;
    margin-right: -0.9375rem;
  }
}

.footer {
  position: absolute;
  width: 100%;
  bottom: 0;
  padding-top: 3.125rem;
  padding-bottom: 3.125rem;
}
@media (max-width: 96em) {
  .footer {
    padding-top: 1.25rem;
    padding-bottom: 1.25rem;
  }
}
.socials {
  display: flex;
  gap: 0.875rem;
}
.socials__item {
  transition: transform 0.3s ease 0s;
}
@media (any-hover: hover) {
  .socials__item:hover {
    transform: scale(1.08);
  }
}
.socials__icon {
  width: 3rem;
  height: 3rem;
}
.socials__icon img {
  width: 100%;
}
@media (max-width: 48em) {
  .socials__icon {
    width: 2.1875rem;
    height: 2.1875rem;
  }
}